body > footer {
    border-top: 2px solid #dddddd;
    height: var(--footer-height);
    font-size: 16px;
    color: #393939;
    display: flex;
    justify-content: space-between;
    align-items: center;


    @media (max-width: 570px) {
        .copyright__extra {
            display: none;
        }
    }
}
